html,
body,
ul,
li {
  margin: 0;
  padding: 0;
  list-style: none;
}

body {
  background: #eee;
  overflow: hidden;
  font-family: "Helvetica Neue", Helvetica, "PingFang SC", "Hiragino Sans GB", "Microsoft YaHei", "\5FAE\8F6F\96C5\9ED1", Arial, sans-serif;
}

.hljs {
  font-family: 'Consolas';
}

div[article] p {
  line-height: 1.8;
}

div[article] a {
  color: #AB0011;
  text-decoration: none;
}

div[article] table {
  width: 100%;
  margin-bottom: 20px;
  border: 1px solid #ddd;
  border-collapse: collapse;
  /* border-left: none; */
  word-break: normal;

}

div[article] th {
  text-align: center;
  font-weight: 700;
  border: 1px solid #ddd;

}

div[article] tr {
  vertical-align: middle;
  text-align: inherit;
  border: 1px solid #ddd;

  font-size: 20px;
  font-size: 16px;

  line-height: 1.7;
}

div[article] tr:nth-of-type(2n) {
  background-color: hsla(0, 0%, 71%, .1);

}

div[article] td {
  text-align: center;
  padding: 8px;
  border: 1px solid #ddd;
  line-height: 20px;
  vertical-align: middle;
}

div[article] p code {
    color: #476582;
    padding: .1rem .5rem;
    margin: 0 5px;
    height: 20px;
    word-wrap: none;
    word-break: normal;
    font-weight: 300;
    background-color: rgba(27,31,35,.05);
    border-radius: 3px;
    font-family: source-code-pro,Menlo,Monaco,Consolas,Courier New,monospace;
}

div[article] pre {
  font-family: source-code-pro,Menlo,Monaco,Consolas,Courier New,monospace;
}

div[article] .hljs {
 background: #020207;
 border-radius: 6px;
}

@media screen and (max-width: 1060px) {
  div[article] .hljs {
    border-radius: 0px;
  }
  div[article] h1 {
    font-size: 20px;
  }

  div[article] h2 {
    font-size: 18px;
  }

  div[article] p {
    line-height: 1.7;
  }

  div[article] h3 {
    font-size: 16px !important;
  }

  div[article] {
    font-size: 14px;
  }

  div[article] code {
    font-size: 12px;
    font-family: source-code-pro,Menlo,Monaco,Consolas,Courier New,monospace;

  }
}
